export function removeBaselineMedianCascade(
  x,
  fs = 400,
  {
    // window lengths in seconds; tuned for fs=400
    win1Sec = 0.2,
    win2Sec = 0.6,
    // if true, remove any tiny DC leftover after subtraction
    removeResidualDC = false,
    // replace NaN/Inf via linear interpolation
    fixNonFinite = true,
  } = {},
) {
  if (!Array.isArray(x) && !(x instanceof Float64Array)) {
    throw new TypeError('x must be an array-like of numbers');
  }
  const N = x.length;
  if (N === 0) return [];

  // Prepare working copy in float64 for numerical stability
  let xWork = toFloat64Array(x);

  // Sanitize non-finite values
  if (fixNonFinite) {
    xWork = fixNonFiniteLinear(xWork);
  }

  // Compute odd window sizes, clamp to valid range
  let w1 = makeOdd(Math.max(3, Math.round(win1Sec * fs)));
  let w2 = makeOdd(Math.max(3, Math.round(win2Sec * fs)));
  w1 = clampOdd(w1, N);
  w2 = clampOdd(w2, N);

  // First median (suppresses QRS)
  const m1 = medianFilterReflect(xWork, w1);

  // Second median (suppresses T waves) -> baseline estimate
  const baseline = medianFilterReflect(m1, w2);

  // Subtract baseline
  const y = new Float64Array(N);
  for (let i = 0; i < N; i++) y[i] = xWork[i] - baseline[i];

  // Optional tiny DC removal (robust)
  if (removeResidualDC) {
    const med = robustMedian(y);
    for (let i = 0; i < N; i++) y[i] -= med;
  }

  return Array.from(y);
}
function makeOdd(n) {
  return n % 2 === 0 ? n + 1 : n;
}
function clampOdd(w, N) {
  if (w <= 3) return 3;
  if (w > N) return N % 2 === 1 ? N : N - 1;
  return w % 2 === 1 ? w : w - 1;
}

function toFloat64Array(arr) {
  if (arr instanceof Float64Array) return arr.slice();
  const out = new Float64Array(arr.length);
  for (let i = 0; i < arr.length; i++) out[i] = Number(arr[i]);
  return out;
}

function robustMedian(a) {
  const tmp = Array.from(a);
  tmp.sort((u, v) => u - v);
  const mid = Math.floor(tmp.length / 2);
  return tmp.length % 2 ? tmp[mid] : 0.5 * (tmp[mid - 1] + tmp[mid]);
}

function fixNonFiniteLinear(x) {
  const N = x.length;
  const y = new Float64Array(N);
  // Copy and mark finite
  const finite = new Array(N);
  for (let i = 0; i < N; i++) {
    const v = x[i];
    const isFin = Number.isFinite(v);
    y[i] = isFin ? v : 0;
    finite[i] = isFin;
  }

  // Find first finite
  let firstFin = 0;
  while (firstFin < N && !finite[firstFin]) firstFin++;
  if (firstFin === N) {
    // All non-finite -> return zeros
    return y;
  }
  // Fill leading
  for (let i = 0; i < firstFin; i++) y[i] = y[firstFin];

  // Interpolate interior gaps
  let i = firstFin;
  while (i < N) {
    if (finite[i]) {
      i++;
      continue;
    }
    const start = i - 1;
    // Find end of gap
    let j = i + 1;
    while (j < N && !finite[j]) j++;
    const end = j < N ? j : N - 1;
    const y0 = y[start];
    const y1 = y[end];
    const len = end - start;
    for (let k = 1; k < len; k++) {
      y[start + k] = y0 + (y1 - y0) * (k / len);
    }
    // If trailing to end with no finite, fill with last known
    if (j === N) {
      for (let t = start + 1; t < N; t++) y[t] = y0;
      break;
    }
    i = j + 1;
  }
  return y;
}

function medianFilterReflect(x, w) {
  const N = x.length;
  if (w <= 1 || N === 0) return Array.from(x);
  const r = (w - 1) >> 1;

  // Build reflected padded array: [leftPad | x | rightPad]
  const leftPad = reflectLeft(x, r);
  const rightPad = reflectRight(x, r);
  const padded = new Float64Array(r + N + r);
  padded.set(leftPad, 0);
  padded.set(x, r);
  padded.set(rightPad, r + N);

  // Initial window (over padded indices [0 .. w-1])
  const window = Array(w);
  for (let i = 0; i < w; i++) window[i] = padded[i];
  window.sort((a, b) => a - b);

  const out = new Float64Array(N);
  const mid = (w - 1) >> 1;

  // For each output i, window covers padded [i .. i + w - 1]
  for (let i = 0; i < N; i++) {
    // Median for current window
    out[i] = window[mid];

    // Slide window: remove padded[i], add padded[i + w]
    if (i < N - 1) {
      const outVal = padded[i];
      const inVal = padded[i + w];

      // Remove one occurrence of outVal
      let idx = lowerBound(window, outVal);
      // lowerBound should point to a value >= outVal; ensure we remove matching one
      while (idx < window.length && window[idx] !== outVal) idx++;
      if (idx < window.length) window.splice(idx, 1);
      else {
        // Fallback: if not found due to FP quirks, remove closest
        idx = lowerBound(window, outVal);
        window.splice(Math.min(idx, window.length - 1), 1);
      }

      // Insert inVal keeping sorted order
      const ins = lowerBound(window, inVal);
      window.splice(ins, 0, inVal);
    }
  }

  return Array.from(out);
}
function reflectLeft(x, r) {
  // Take first r samples and mirror them
  const out = new Float64Array(r);
  for (let i = 0; i < r; i++) out[i] = x[r - 1 - i];
  return out;
}
function reflectRight(x, r) {
  // Take last r samples and mirror them
  const out = new Float64Array(r);
  const N = x.length;
  for (let i = 0; i < r; i++) out[i] = x[N - 1 - i];
  return out;
}
function lowerBound(arr, value) {
  let lo = 0,
    hi = arr.length;
  while (lo < hi) {
    const mid = (lo + hi) >>> 1;
    if (arr[mid] < value) lo = mid + 1;
    else hi = mid;
  }
  return lo;
}
export function applyNotch50Hz(
  x,
  fs = 400,
  { f0 = 50, Q = 35, zeroPhase = true, padMult = 3, fixNonFinite = true } = {},
) {
  let xWork = toFloat64Array(x);
  if (fixNonFinite) xWork = fixNonFiniteLinear(xWork);

  const nyq = fs / 2;
  if (!(f0 > 0 && f0 < nyq)) {
    return Array.from(xWork);
  }
  if (!Number.isFinite(Q) || Q < 5) Q = 5;
  if (Q > 1000) Q = 1000;

  // --- Design biquad
  const { b, a } = designNotchBiquad(f0, fs, Q);

  // --- Filtering
  let y;
  if (zeroPhase) {
    y = filtfiltBiquad(xWork, b, a, padMult);
  } else {
    y = iirBiquadDF2T(xWork, b, a); // causal only (phase shift!)
  }
  return Array.from(y);
}

function designNotchBiquad(f0, fs, Q) {
  const w0 = 2 * Math.PI * (f0 / fs);
  const cosw0 = Math.cos(w0);
  const sinw0 = Math.sin(w0);
  const alpha = sinw0 / (2 * Q);

  // Notch: zeros on unit circle at w0, poles set by alpha
  let b0 = 1;
  let b1 = -2 * cosw0;
  let b2 = 1;
  let a0 = 1 + alpha;
  let a1 = -2 * cosw0;
  let a2 = 1 - alpha;

  // Normalize so a0 = 1
  b0 /= a0;
  b1 /= a0;
  b2 /= a0;
  a1 /= a0;
  a2 /= a0;
  a0 = 1;

  return { b: [b0, b1, b2], a: [a0, a1, a2] };
}

function iirBiquadDF2T(x, b, a) {
  const N = x.length;
  const y = new Float64Array(N);
  const b0 = b[0],
    b1 = b[1],
    b2 = b[2];
  const a1 = a[1],
    a2 = a[2];
  let s1 = 0,
    s2 = 0; // states

  for (let n = 0; n < N; n++) {
    const in_n = x[n];
    const out = b0 * in_n + s1;
    s1 = b1 * in_n + s2 - a1 * out;
    s2 = b2 * in_n - a2 * out;
    y[n] = out;
  }
  return y;
}

function filtfiltBiquad(x, b, a, padMult = 3) {
  const N = x.length;
  if (N === 0) return new Float64Array(0);

  // For biquad, filter order = 2; transient len ~ 3*(order)
  const basePad = Math.max(b.length, a.length) - 1;
  const padLen = Math.max(6, Math.min(padMult * basePad, Math.floor(N / 2))); // never exceed N/2

  // Build reflected padded signal
  const left = reflectLeft(x, padLen);
  const right = reflectRight(x, padLen);
  const padded = new Float64Array(padLen + N + padLen);
  padded.set(left, 0);
  padded.set(x, padLen);
  padded.set(right, padLen + N);

  // Forward filter
  let y = iirBiquadDF2T(padded, b, a);

  // Reverse
  y = reverseFloat64(y);

  // Backward filter (same coefficients)
  y = iirBiquadDF2T(y, b, a);

  // Reverse back
  y = reverseFloat64(y);

  // Remove padding
  return y.subarray(padLen, padLen + N);
}

function reverseFloat64(arr) {
  const N = arr.length;
  const out = new Float64Array(N);
  for (let i = 0; i < N; i++) out[i] = arr[N - 1 - i];
  return out;
}

export function applyLowpassButterworth(
  x,
  fs = 400,
  {
    fc = 40, // cutoff frequency (Hz)
    order = 4, // even order (2, 4, 6, ...)
    zeroPhase = true, // filtfilt
    padMult = 6, // padding multiplier for filtfilt
    fixNonFinite = true,
  } = {},
) {
  if (!Array.isArray(x) && !(x instanceof Float64Array)) {
    throw new TypeError('x must be an array-like of numbers');
  }
  const N = x.length;
  if (N === 0) return [];

  let xWork = toFloat64Array(x);
  if (fixNonFinite) xWork = fixNonFiniteLinear(xWork);

  // Sanity checks
  if (order < 2) order = 2;
  if (order % 2 !== 0) order += 1; // enforce even
  const nyq = fs / 2;
  if (!(fc > 0 && fc < nyq)) {
    // out of range -> return copy
    return Array.from(xWork);
  }

  // Build Butterworth cascade (SOS) as array of {b:[b0,b1,b2], a:[1,a1,a2]}
  const sos = designButterworthLPSOS(fc, fs, order);

  // Filter
  const y = zeroPhase ? filtfiltCascadeSOS(xWork, sos, padMult) : iirCascadeDF2T(xWork, sos); // causal (has phase shift)

  return Array.from(y);
}

function designButterworthLPSOS(fc, fs, orderEven) {
  const m = orderEven / 2;
  const Qs = butterworthQs(orderEven); // length m
  const sos = [];
  for (let k = 0; k < m; k++) {
    sos.push(designLowpassBiquadRBJ(fc, fs, Qs[k]));
  }
  return sos;
}

function butterworthQs(N) {
  const m = Math.floor(N / 2);
  const Qs = new Array(m);
  for (let k = 1; k <= m; k++) {
    const zeta = Math.sin(((2 * k - 1) * Math.PI) / (2 * N));
    Qs[k - 1] = 1 / (2 * zeta);
  }
  // Sorting ascending Q can slightly improve numerical robustness in cascade
  Qs.sort((a, b) => a - b);
  return Qs;
}

function designLowpassBiquadRBJ(fc, fs, Q) {
  const w0 = 2 * Math.PI * (fc / fs);
  const cosw0 = Math.cos(w0);
  const sinw0 = Math.sin(w0);
  const alpha = sinw0 / (2 * Q);

  let b0 = (1 - cosw0) / 2;
  let b1 = 1 - cosw0;
  let b2 = (1 - cosw0) / 2;

  let a0 = 1 + alpha;
  let a1 = -2 * cosw0;
  let a2 = 1 - alpha;

  // Normalize so a0 = 1
  b0 /= a0;
  b1 /= a0;
  b2 /= a0;
  a1 /= a0;
  a2 /= a0;
  a0 = 1;

  return { b: [b0, b1, b2], a: [a0, a1, a2] };
}

function iirCascadeDF2T(x, sos) {
  let y = toFloat64Array(x);
  for (let i = 0; i < sos.length; i++) {
    const { b, a } = sos[i];
    y = iirBiquadDF2T(y, b, a);
  }
  return y;
}

function filtfiltCascadeSOS(x, sos, padMult = 6) {
  const N = x.length;
  if (N === 0) return new Float64Array(0);

  // Estimate transient length ~ 3 samples per biquad (rough heuristic)
  const padLen = Math.max(12, Math.min(padMult * 3 * sos.length, Math.floor(N / 2)));

  // Build reflected padded signal
  const left = reflectLeft(x, padLen);
  const right = reflectRight(x, padLen);
  const padded = new Float64Array(padLen + N + padLen);
  padded.set(left, 0);
  padded.set(x, padLen);
  padded.set(right, padLen + N);

  // Forward cascade
  let y = iirCascadeDF2T(padded, sos);

  // Reverse
  y = reverseFloat64(y);

  // Backward cascade (same direction through the cascade)
  y = iirCascadeDF2T(y, sos);

  // Reverse back and unpad
  y = reverseFloat64(y);
  return y.subarray(padLen, padLen + N);
}

export function applyBandpassButterworth(
  x,
  fs = 400,
  {
    f1 = 5, //    (Hz)
    f2 = 15, //    (Hz)
    orderHP = 2, //   HP (2,4,6,...)
    orderLP = 2, //   LP (2,4,6,...)
    zeroPhase = true, // filtfilt   
    padMult = 6, //    filtfilt
    fixNonFinite = true,
  } = {},
) {
  if (!Array.isArray(x) && !(x instanceof Float64Array)) {
    throw new TypeError('x must be an array-like of numbers');
  }
  const N = x.length;
  if (N === 0) return [];

  //  
  let xWork = toFloat64Array(x);
  if (fixNonFinite) xWork = fixNonFiniteLinear(xWork);

  //   / 
  const nyq = fs / 2;
  if (!(f1 > 0) || !(f2 > 0) || !(f1 < f2) || !(f2 < nyq)) {
    //       
    return Array.from(xWork);
  }
  if (orderHP < 2) orderHP = 2;
  if (orderLP < 2) orderLP = 2;
  if (orderHP % 2 !== 0) orderHP += 1;
  if (orderLP % 2 !== 0) orderLP += 1;

  //   SOS:  HP(f1),  LP(f2)
  const sosHP = designButterworthHPSOS(f1, fs, orderHP);
  const sosLP = designButterworthLPSOS(f2, fs, orderLP); //   3
  const sos = sosHP.concat(sosLP);

  // 
  const y = zeroPhase
    ? filtfiltCascadeSOS(xWork, sos, padMult) //  
    : iirCascadeDF2T(xWork, sos); //  (  )

  return Array.from(y);
}

function designButterworthHPSOS(fc, fs, orderEven) {
  const m = orderEven / 2;
  const Qs = butterworthQs(orderEven); //   3
  const sos = [];
  for (let k = 0; k < m; k++) {
    sos.push(designHighpassBiquadRBJ(fc, fs, Qs[k]));
  }
  return sos;
}

function designHighpassBiquadRBJ(fc, fs, Q) {
  const w0 = 2 * Math.PI * (fc / fs);
  const cosw0 = Math.cos(w0);
  const sinw0 = Math.sin(w0);
  const alpha = sinw0 / (2 * Q);

  let b0 = (1 + cosw0) / 2;
  let b1 = -(1 + cosw0);
  let b2 = (1 + cosw0) / 2;

  let a0 = 1 + alpha;
  let a1 = -2 * cosw0;
  let a2 = 1 - alpha;

  //  ,  a0 = 1
  b0 /= a0;
  b1 /= a0;
  b2 /= a0;
  a1 /= a0;
  a2 /= a0;
  a0 = 1;

  return { b: [b0, b1, b2], a: [a0, a1, a2] };
}
