import {
  adaptivePeakPick,
  bandpassQRS,
  fuseCandidates,
  keepHillsOnlyV2,
  ptFeature,
  refineOnRaw,
  removeBaseline,
  robustNormalize,
  rrPostprocess,
  tkeoCandidatePeaks,
  tkeoFeature,
} from './signalConversionGPT';
import { toFloat32 } from './utils';

export function detectRPeaks(signal, channel) {
  const fs = 400;
  const signalData = signal[channel].data.map((item) => item[1]);
  const signalTime = signal[channel].data.map((item) => item[0]);
  const x0 = toFloat32(signalData);
  const xBaseRemoved = removeBaseline(x0, fs, { method: 'hp' });
  const xBP = bandpassQRS(xBaseRemoved, fs, { hp: 8, lp: 40 });
  const xNR = robustNormalize(xBP, {}).signal || robustNormalize(xBP, {});
  const wPT = ptFeature(xNR, fs, {});
  const sTKEO = tkeoFeature(xNR, fs, {});
  const candPT = adaptivePeakPick(wPT, fs, {});
  const candTK = tkeoCandidatePeaks(xNR, fs, Object.assign({ returnFeature: false }, {}));
  const fusedMeta = fuseCandidates([candPT, candTK], fs, {
    feats: [wPT, sTKEO],
    xRef: xNR,
    mergeTolMs: 100,
    returnMeta: true,
  });
  const fusedIdx = fusedMeta.indices;
  const refined = refineOnRaw(x0, fusedIdx, fs, { xBandpass: xBP, ...{} });
  const rr = rrPostprocess(xNR, refined, fs, { feat: wPT, ...{} });
  const rClean = keepHillsOnlyV2(xNR, rr, fs);
  return {
    series: signalTime.map((item, i) => [item, rClean.includes(i) ? 100 : 0]),
    data: Array.from(rClean),
  };
}
