/* eslint-env worker */
/* eslint-disable no-restricted-globals */

import { analyzeATN } from '../algorithms/ATNAlgorithm/mainSignalConversion.js';
import { analyzeCMD } from '../algorithms/CMDAlgorithm/mainConversionCMD.js';
import { detectRPeaks } from '../algorithms/GPTAlgorithm/mainConversionGPT.js';
import { analyzeECGKHO } from '../algorithms/KHOAlgorithm/mainSignalConversion.js';
import { preprocessSignal } from '../preprocessSignal/preprocessSignal.js';
import { analyzePT } from '../algorithms/PTAlgorithm/mainConversionPT.js';

const registry = {
  PT: (series, channel) =>
    analyzePT([{ data: preprocessSignal(series, channel).newPreprocess.newFourth }], 1, 0).peaks
      .data,
  PTPlus: (series, channel) =>
    analyzePT([{ data: preprocessSignal(series, channel).biggestPreprocess.detrended }], 2, 0)
      .peaksPlusPlus.data,
  KHO: (series, channel) =>
    analyzeECGKHO([{ data: preprocessSignal(series, channel).mvmFiltered }], 0).peaks.data,
  ATN: (series, channel) =>
    analyzeATN([{ data: preprocessSignal(series, channel).mvmFiltered }], 0).peaks.data,
  CMD1: (series, channel) =>
    analyzeCMD([{ data: preprocessSignal(series, channel).newPreprocess.newSecond }], 1, 0).data,
  CMD2: (series, channel) =>
    analyzeCMD([{ data: preprocessSignal(series, channel).notchFilteredML }], 2, 0).data,
  GPT: (series, channel) =>
    detectRPeaks([{ data: preprocessSignal(series, channel).biggestPreprocess.bp }], 0).data,
};

addEventListener('message', (e) => {
  const { name, input, channel } = e.data;
  try {
    const fn = registry[name];
    if (!fn) throw new Error(`Unknown task name: ${name}`);
    const result = fn(input, channel);
    postMessage({ ok: true, name, result });
  } catch (err) {
    postMessage({ ok: false, name, error: String(err), stack: err?.stack });
  }
});
